import skating_video from "../../assets/video.mp4";
import knossi_png from "../../assets/knossi.png";
import React from "react";

export const MainVideo = () => {
    return (
        <div>
            <div className="container">
                <video id="background-video" loop autoPlay muted>
                    <source src={skating_video} type="video/mp4"/>
                    Your browser does not support the video tag.
                </video>
                <div className="overlay">
                    <div className="logo"><img src={knossi_png} alt="Logo"/></div>
                    {/*<div className="video-title" title="Project 202">Project 202</div>*/}
                </div>
            </div>
        </div>
    );
}