import React from "react";
import './HeaderMenu.css';
import {Link} from "react-router-dom";

export const HeaderMenu = () => {
    return (
        <nav>
            <div className="main-menu">
                <Link to="/"><div className="nav-link header">Project 202</div></Link>
                <div className="spacer" />
                <Link to="/about"><div className="nav-link">About</div></Link>
                <Link to="/philosophy"><div className="nav-link">Philosophy</div></Link>
                <Link to="/contact"><div className="nav-link">Contact</div></Link>
            </div>
        </nav>
    );
}